import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;
import Vue from 'vue'
import App from './App'
import store from './store/store.js'

import {
	$http
} from '@escook/request-miniprogram'

uni.$http = $http

// 请求根路径
// $http.baseUrl = 'http://baozi-backend.online:7000'
$http.baseUrl = 'http://111.230.69.234:7000'
// $http.baseUrl = 'http://127.0.0.1:7000'
// $http.baseUrl = 'http://127.0.0.1:8080'
// $http.baseUrl = 'https://api-hmugo-web.itheima.net'

// 请求拦截器
$http.beforeRequest = function(options) {
	uni.showLoading({
		title: '数据加载中...'
	})
	// 判断请求的是否为有权限的 API 接口
	if (options.url.indexOf('/cart/') !== -1 ||
		options.url.indexOf('/address') !== -1 ||
		options.url.indexOf('/my/') !== -1) {
		// 为请求头添加身份认证字段
		options.header = {
			// 字段的值可以直接从 vuex 中进行获取
			Authorization: store.state.m_user.token,
			user_id: store.state.m_user.user_id,
		}
	}
	// if (options.url.indexOf('/address') !== -1) {
	// 	// 为请求头添加身份认证字段
	// 	options.header = {
	// 		// 字段的值可以直接从 vuex 中进行获取
	// 		Authorization: store.state.m_user.token,
	// 		user_id: store.state.m_user.user_id,
	// 	}
	// }
}
// 响应拦截器
$http.afterRequest = function() {
	uni.hideLoading()
}

// 封装错误弹窗的方法
uni.$showMsg = function(title = '数据加载失败！', duration = 1500) {
	uni.showToast({
		title,
		duration,
		icon: 'none',
	})
}

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
	...App,
	store,
})
app.$mount()